import startSearch from './startSearch';

export default function languageСheck(str) {
  let match = /^[а-яё]*$/i.test(str);
  if(match){
    let request = new Request('https://translate.yandex.net/api/v1.5/tr.json/translate?key=trnsl.1.1.20200509T213248Z.a20a610899194d8c.8324ae383281871e5403915018185ac6cbc686fa&text=' + str + '&lang=ru-en');
    translate(request);
  }
  else{
    let request = new Request('https://www.omdbapi.com/?s='+str+'&apikey=b43b13bc');
    startSearch(request);
  }
}
